<template>
  <div>
    <el-page-header @back="goBack" content="搜索详情" class="page-header">
    </el-page-header>
    <IframeVideo :url="iframeUrl" ref="IframeVideo"></IframeVideo>
    <div>
      <el-button
          :class="active == item.id ? 'active' : ''"
          style="width: 100px"
          type="info"
          plain
          v-for="item in videoParseUrls"
          :key="item.id"
          @click="changeParseUrl(item.url, item.id)"
      >
        {{ item.name }}
      </el-button>
    </div>
    <hr/>
  </div>
</template>

<script scoped>
import IframeVideo from '@/views/display/components/IframeVideo'
import {routerUtils} from '@/utils/RouterUtils.js'

export default {
  name: 'DisplayNew',
  components: {
    IframeVideo,
  },
  props: {
    id: String,
  },
  data: function () {
    return {
      active: 0,
      parseUrl: 'https://jx.xmflv.com/?url=',
      videoParseUrls: [
        {
          id: '0',
          url: 'https://jx.xmflv.com/?url=',
          name: '网站1',
          isDefault: true,
        },
        {
          id: '1',
          url: 'https://okjx.cc/?url=',
          name: '网站2',
          isDefault: false,
        },
        {
          id: '2',
          url: 'https://jx.aidouer.net/?url=',
          name: '网站3',
          isDefault: false,
        },
        {
          id: '3',
          url: 'https://jx.jsonplayer.com/player/?url=',
          name: '网站4',
          isDefault: false,
        },
        {
          id: '4',
          url: 'https://jx.bozrc.com:4433/player/?url=',
          name: '网站5',
          isDefault: false,
        },
        {
          id: '5',
          url: 'https://api.okjx.cc:3389/jx.php?url=',
          name: '网站6',
          isDefault: false,
        },
      ],
      iframeUrl: '/shphuang/unvideo.html',
      url: '',
    }
  },
  methods: {
    goBack: function () {
      routerUtils.back(1)
    },
    changeParseUrl: function (url, id) {
      this.parseUrl = url
      this.active = id
      this.displayVideo()
    },
    displayVideo: function () {
      this.iframeUrl = this.parseUrl + this.url
      console.log(this.iframeUrl)
      console.log(this.$refs.IframeVideo.reloadIframe())
    },
  },

  created() {
    console.log('DisplayNew create...')
  },

  mounted() {
    this.url = this.id

    this.displayVideo()
    console.log('DisplayNew mounted...')

  },
}
</script>

<style lang="css" scoped>
.page-header {
  height: 40px;
  line-height: 40px;
  background-color: rgb(239, 241, 242);
}

.active {
  background-color: #909399 !important;
  border-color: #909399 !important;
  color: #fff !important;
}
</style>
